___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: rgb(8, 159, 170);
  --primary-color-darker: rgb(5, 86, 92);
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background: var(--primary-color);
  font-family: "Fira Code", sans-serif;
  font-size: 1.3em;
  line-height: 1.5em;
}

.container {
  max-width: 640px;
  margin: 50px auto;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
}

form input,
form label,
form button {
  display: block;
  width: 100%;
  margin-bottom: 10px;
}

form input {
  font-size: 24px;
  height: 50px;
  padding: 0 20px;
}

form input:focus {
  outline: 1px solid var(--primary-color);
}

form button {
  border: none;
  background: var(--primary-color);
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  height: 50px;
  cursor: pointer;
  margin-top: 35px;
}

form button:hover {
  background: var(--primary-color-darker);
}

.container h1 {
  text-align: center;
  margin-bottom: 40px;
}

.generated-password {
  font-size: 2em;
  color: var(--primary-color);
  text-align: center;
  font-weight: bold;
  margin-bottom: 40px;
  letter-spacing: 5px;
}

input[type="checkbox"] {
  width: 15px;
  height: 15px;
}

button {
  font-size: 1em;
  margin-top: 20px;
  display: block;
}
